  font-weight: 700; }

p {
  font-size: 16px;
  font-weight: normal;
  line-height: 1.4;
  margin-left: 0;
  color: #484d56; }

/* force styles of client inserted tel anchors */
a[href^=tel] {
  color: #ef4036;
  font-weight: bold;
  text-decoration: none !important; }

.wrapper {
  background: #ffffff;
  border-collapse: collapse;
